@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap");

.App {
  text-align: center;
  background-color: #2a2e3c;
  width: 100vw;
  height: 85vh;
  display: flex;
  padding: 15vh 0 0 0;
  align-items: flex-start;
  justify-content: center;
  font-family: "Quicksand";
  overflow: hidden;
}

.App-logo {
  height: 5em;
  border-radius: 2.5em;
  pointer-events: none;
  user-select: none;
}

.App-header {
  background-color: white;
  height: 325px;
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  border-radius: 15px;
  padding: 2em 0 2em 0;
  box-sizing: border-box;
  box-shadow: 0 1px 60px 35px #1e2026;
}
.name {
  font-weight: 400;
  font-size: 2.6em;
  letter-spacing: 0.1em;
  color: black;
  margin: 3px 0 0px 0;
  pointer-events: none;
  user-select: none;
}
.phrase {
  font-weight: 300;
  font-size: 1em;
  color: grey;
  margin: 0 0 1.8em 0;
  pointer-events: none;
  user-select: none;
}
.button {
  font-family: Quicksand;
  border: none;
  background: #85ffc2;
  margin: 0 0 0 0;
  font-weight: 500;
  font-size: 0.8em;
  color: #2a2e3c;
  letter-spacing: 0.4em;
  height: 3.5em;
  width: 15em;
  border-radius: 2em;
  box-shadow: 0 15px 15px -10px #b8b8b8;
  transition: all 0.1s ease;
  outline: none;
}

button:hover {
  filter: brightness(110%);
  /* transform: scale(1.03); */
  cursor: pointer;
}
button:active {
  filter: brightness(100%);
  transform: scale(1);
  box-shadow: none;
}

.download-text {
  padding: 0 0 0 5px;
}
